import { Injectable } from "@angular/core";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";
import { retry } from "rxjs/operators";

@Injectable({
	providedIn: "root",
})
export class ChallengeAnchorService {
	api_url = environment.api_url;

	constructor(private http: HttpClient) {}

	async associateAnchorAndSmartCity(anchorId: number, smartCityId: number) {
		return await this.http
			.post(this.api_url + "/anchors_and_smart_cities/create", {
				data: {
					frg_anchor_id: anchorId,
					frg_smart_city_id: smartCityId,
				},
			})
			.toPromise();
	}

	async getSeekerCity(seekerId) {
		return await this.http
			.post(this.api_url + "/seekers/select_slug", {
				slug: {
					id: seekerId,
				},
			})
			.toPromise();
	}

	async associateChallengewithCity(idParams: any) {
		let checkIfAssociationAlreadyExist: any = await this.http
			.post(this.api_url + "/challenges_and_smart_cities/select_mul", {
				filter: {
					frg_challenge_id: idParams.challengeId,
					frg_smart_city_id: idParams.smartCityId,
				},
			})
			.toPromise();
		// console.log('challenge association exist', checkIfAssociationAlreadyExist.status);
		if (!checkIfAssociationAlreadyExist.status) {
			await this.http
				.post(this.api_url + "/challenges_and_smart_cities/create", {
					data: {
						frg_challenge_id: idParams.challengeId,
						frg_smart_city_id: idParams.smartCityId,
					},
				})
				.toPromise();
		}
		return;
	}

	async associateChallengeSectorwithCity(idParams: any) {
		let checkIfAssociationAlreadyExist: any = await this.http
			.post(this.api_url + "/smart_cities_and_challenge_sectors/select_mul", {
				filter: {
					frg_challenge_sector_id: idParams.sectorId,
					frg_smart_city_id: idParams.smartCityId,
				},
			})
			.toPromise();
		// console.log('challenge sector association exist', checkIfAssociationAlreadyExist.status);
		if (!checkIfAssociationAlreadyExist.status) {
			await this.http
				.post(this.api_url + "/smart_cities_and_challenge_sectors/create", {
					data: {
						frg_challenge_sector_id: idParams.sectorId,
						frg_smart_city_id: idParams.smartCityId,
					},
				})
				.toPromise();
		}
	}

	async createAnchorForChallenge(postData) {
		return await this.http.post(this.api_url + "/anchors/create", postData).toPromise();
	}

	async getChallenge(id: number) {
		return await this.http.post(this.api_url + "/challenges/select", { slug: { id: id } }).toPromise();
	}

	async getSectorIdsFollowedByUser(cityAdminId: number) {
		let resp: any = await this.http
			.post(this.api_url + "/challenge_sectors_seekers/select_mul", {
				filter: {
					frg_seeker_id: cityAdminId,
				},
			})
			.toPromise();
		let sectorIds: number[] = resp.data.map((e) => e.frg_challenge_sector_id);
		return sectorIds;
	}

	async sendNotifications(anchorId: number) {
		return await this.http
			.post(this.api_url + "/anchors/new_anchor_notification", {
				slug: { id: anchorId },
			})
			.toPromise();
	}
}
