import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { ChallengeService } from "../challenge.service";
import { environment } from "../../../environments/environment";
import { Location } from "@angular/common";
import { Select } from "@ngxs/store";
import { LoggedInUser } from "../../state/model/loggedinuser.model";
import { LoggedInUserState, LoggedInUserStateModel } from "../../state/loggedinuser.state";
import { Observable } from "rxjs";
@Component({
	selector: "app-challenge-overview",
	templateUrl: "./challenge-overview.component.html",
	styleUrls: ["./challenge-overview.component.css"],
})
export class ChallengeOverviewComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	currentUser: LoggedInUser;

	isAuthor: boolean = false;
	loading: boolean;
	anchorData: any;
	challengeData: any;
	anchorId: number;
	challengeId: number;
	relatedSolutionIds: number[];
	otherAnchors: any[] = [];
	solutions: any;
	serveUrl: string = environment.server_url;
	constructor(private _anchor: ChallengeService, private route: ActivatedRoute, private location: Location) {
		this.route.params.subscribe((params) => {
			this.anchorId = params.anchorid;
			this.challengeId = params.id;
		});
		this.loggedInUser$.subscribe((resp) => {
			this.currentUser = resp.LoggedInUser;
		});
	}

	async ngOnInit() {
		this.loading = true;
		await this.fetchAnchorAndChallenge();
		this.loading = false;
	}

	async fetchAnchorAndChallenge() {
		try {
			this.anchorData = await this._anchor.getAnchorById(this.anchorId);
			this.isAuthor = this.anchorData.data.frg_seeker_id.id == this.currentUser.userTypeId ? true : false;
			this.challengeData = await this._anchor.getChallenge(this.challengeId);
			this.otherAnchors = this.challengeData.data.anchors.filter((e) => {
				if (e.id != this.anchorId) return e;
			});
			let anchorsByChallengeId: any = await this._anchor.getAnchorsForChallenge(this.challengeId);
			this.otherAnchors = anchorsByChallengeId.data.filter((e) => {
				if (e.id != this.anchorId) return e;
			});
			this.relatedSolutionIds = this.challengeData.data.solutions.map((e) => e.id);
			// this.solutions = await this._anchor.getRelatedSolutions(relatedSolutionIds)
		} catch (err) {
			console.log(err);
		}
	}

	goBack() {
		this.location.back();
	}
}
