import { Injectable } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class SolutionWidgetService {
  api_url = environment.api_url;

  constructor(private http: HttpClient) { }

  async getRelatedSolutions(challengeId: number[]) {
    return await this.http.post(this.api_url + '/solutions/select_mul', { slug: { id: challengeId } }).toPromise();
  }
}
