import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { ChallengeComponent } from "./challenge.component";
import { CreateChallengeComponent } from "./create-challenge/create-challenge.component";
import { ChallengesAnchorComponent } from "./challenges-anchor/challenges-anchor.component";
import { ChallengeOverviewComponent } from "./challenge-overview/challenge-overview.component";
import { AnchorChallengeComponent } from "./anchor-challenge/anchor-challenge.component";
import { PermissionGuard } from "../permission.guard";
import { DirtyCheckGuard } from "./dirty-check.guard";
const routes: Routes = [
	{
		path: "",
		component: ChallengeComponent,
		children: [
			{
				path: "",
				redirectTo: "/discover/challenges",
				pathMatch: "full",
			},
			{
				path: "create",
				component: CreateChallengeComponent,
				canActivate: [PermissionGuard],
				data: {
					permission: ["seeker"],
				},
			},
			{
				path: ":id",
				component: ChallengesAnchorComponent,
				pathMatch: "full",
			},
			{
				path: ":id/anchor/add",
				component: AnchorChallengeComponent,
				canDeactivate: [DirtyCheckGuard],
			},
			{
				path: ":id/anchor/:anchorid",
				component: ChallengeOverviewComponent,
			},
		],
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class ChallengeRoutingModule {}
