import { Component, OnInit, Input } from "@angular/core";
import { environment } from "../../../environments/environment";
import { ChallengeService } from "../challenge.service";
import { ToastrService } from "ngx-toastr";
import * as _ from "lodash";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../state/loggedinuser.state";
import { LoggedInUser } from "../../state/model/loggedinuser.model";
import { Observable } from "rxjs";

@Component({
	selector: "app-provider-view",
	templateUrl: "./provider-view.component.html",
	styleUrls: ["./provider-view.component.css"],
})
export class ProviderViewComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	@Input() challengeData: any;
	@Input() associatedSolutionsId: number[] = [];
	@Input() anchorLength: any;
	server_url = environment.server_url;
	loading: boolean;
	imagesLoading: boolean;
	relatedSolutionsLoading: boolean;
	relatedSolutions: any;
	anchorLoading: boolean;
	anchors: any;
	solutionsByCurrentUser: any;
	currentUser: LoggedInUser;
	cataloguedFor: string;

	constructor(private challenges: ChallengeService, private toastr: ToastrService) {
		this.loggedInUser$.subscribe((resp) => {
			this.currentUser = resp.LoggedInUser;
		});
	}

	ngOnInit() {
		this.loading = false;
		this.cataloguedFor = JSON.stringify({
			challengeId: this.challengeData.id,
			challengeName: this.challengeData.name,
			sectorId: this.challengeData.frg_challenge_sector_id.id,
			sectorName: this.challengeData.frg_challenge_sector_id.name,
		});
		this.getAnchorAndSolutions();
	}

	async getAnchorAndSolutions() {
		try {
			this.loading = true;
			this.relatedSolutionsLoading = true;
			this.anchors = await this.challenges.getAnchorsForChallenge(this.challengeData.id);
			this.anchors.data.map((anchors) => {
				let smartcityId = anchors.frg_seeker_id.frg_smart_city_id;
				anchors.frg_seeker_id.frg_smart_city_id = anchors.smart_cities.find((city) => smartcityId === city.id);
			});
			this.loading = false;
			this.relatedSolutions = await this.challenges.getRelatedSolutions(this.challengeData.id);
			let solutionIds = _.remove(this.relatedSolutions.data, (solution: any) => {
				return solution.provider_id == this.currentUser.userTypeId;
			});
			solutionIds = solutionIds.map((e) => e.id);
			this.solutionsByCurrentUser = await this.challenges.getSolutions(solutionIds);
			this.relatedSolutionsLoading = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}
}
