import { Component, Input, OnInit, OnChanges } from '@angular/core';
import { SolutionWidgetService } from '../solution-widget/solution-widget.service';
import {environment} from '../../../../environments/environment';

@Component({
  selector: 'solution-widget',
  templateUrl: './solution-widget.component.html',
  styleUrls: ['./solution-widget.component.css']
})
export class SolutionWidgetComponent implements OnChanges {
  @Input() solutionsId: number[];
  serverUrl:string = environment.server_url;
  widgetLoading: boolean;
  solutionData: any;
  constructor(private _solution: SolutionWidgetService) { }

  async ngOnChanges() {
    if (this.solutionsId != undefined) {
      await this.fetchSolutions();
    }
  }

  async fetchSolutions() {
    try {
      this.widgetLoading = true;
      let resp: any = await this._solution.getRelatedSolutions(this.solutionsId);
      this.solutionData = resp.data;
      this.widgetLoading = false;
    } catch (err) {
      console.log(err)
    }
  }

}
