import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { environment } from "../../../environments/environment";
import { ChallengeService } from "../challenge.service";
import { ToastrService } from "ngx-toastr";
import * as _ from "lodash";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../state/loggedinuser.state";
import { LoggedInUser } from "../../state/model/loggedinuser.model";
import { Observable } from "rxjs";

@Component({
	selector: "app-challenges-anchor",
	templateUrl: "./challenges-anchor.component.html",
	styleUrls: ["./challenges-anchor.component.css"],
})
export class ChallengesAnchorComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;

	server_url = environment.server_url;
	id: number;
	challenge: any;
	loading: boolean;
	currentUserType: any;

	constructor(private route: ActivatedRoute, private challenges: ChallengeService, private toastr: ToastrService) {
		this.route.params.subscribe((params) => (this.id = params.id));
		this.loggedInUser$.subscribe((resp) => {
			this.currentUserType = resp.LoggedInUser.userType;
		});
	}

	async ngOnInit() {
		this.getChallenge();
	}

	async getChallenge() {
		try {
			this.loading = true;
			this.challenge = await this.challenges.getChallengeById(this.id);
			this.challenge.data.anchors = this.challenge.data.anchors.filter((c) => {
				if (c.status != "draft") return c;
			});
			// console.log("challenge", this.challenge.data);
			this.loading = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	challengeAnchored(state) {
		if (state) {
			this.getChallenge();
		}
	}
}
