import { Injectable } from "@angular/core";
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, CanDeactivate } from "@angular/router";
import { Observable } from "rxjs";
import { AnchorChallengeComponent } from "./anchor-challenge/anchor-challenge.component";
@Injectable({
	providedIn: "root",
})
export class DirtyCheckGuard implements CanDeactivate<AnchorChallengeComponent> {
	canDeactivate(component: AnchorChallengeComponent, next: ActivatedRouteSnapshot) {
		if (component.createChallengeForm.Step1.dirty || component.createChallengeForm.Step2.dirty || component.createChallengeForm.Step3.dirty) component.ConfirmLeaveComponent();
		else return true;
	}
}
