import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { environment } from "../../../environments/environment";
import { ChallengeService } from "../challenge.service";
import { ToastrService } from "ngx-toastr";
import * as _ from "lodash";
import * as ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../state/loggedinuser.state";
import { Observable } from "rxjs";

@Component({
	selector: "app-seeker-view",
	templateUrl: "./seeker-view.component.html",
	styleUrls: ["./seeker-view.component.css"],
})
export class SeekerViewComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;

	public Editor = ClassicEditor;
	@Input() anchorLength: any;
	@Input() challengeData: any;
	@Input() associatedSolutionsId: number[] = [];
	@Output() anchorAdded = new EventEmitter<boolean>();
	server_url = environment.server_url;
	loading: boolean;
	relatedSolutionsLoading: boolean;
	relatedSolutions: any;
	seekerCity: any;
	anchorLoading: boolean;
	currentSeekerId: number;
	anchors: any;
	anchorsByCurrentUser: any;

	constructor(private challenges: ChallengeService, private toastr: ToastrService) {
		this.loggedInUser$.subscribe((resp) => {
			this.currentSeekerId = resp.LoggedInUser.userTypeId;
		});
		this.Editor.defaultConfig = {
			toolbar: {
				items: ["heading", "|", "bold", "italic", "bulletedList", "numberedList", "link"],
			},
			image: {
				toolbar: ["imageStyle:full", "imageStyle:side", "|", "imageTextAlternative"],
			},
			table: {
				contentToolbar: ["tableColumn", "tableRow", "mergeTableCells"],
			},
			language: "en",
		};
	}

	ngOnInit() {
		this.loading = false;
		this.getAnchorAndSolutions();
	}

	async getAnchorAndSolutions() {
		try {
			this.loading = true;
			this.relatedSolutionsLoading = true;
			this.anchors = await this.challenges.getAnchorsForChallenge(this.challengeData.id);
			this.anchors.data.map((anchors) => {
				let smartcityId = anchors.frg_seeker_id.frg_smart_city_id;
				anchors.frg_seeker_id.frg_smart_city_id = anchors.smart_cities.find((city) => smartcityId === city.id);
			});
			this.anchorsByCurrentUser = _.remove(this.anchors.data, (anchor: any) => {
				return anchor.frg_seeker_id.id == this.currentSeekerId;
			});
			let seekerCityResp: any = await this.challenges.getSeekerCity(this.currentSeekerId);
			this.seekerCity = seekerCityResp.data.frg_smart_city_id;
			this.loading = false;
			this.relatedSolutions = await this.challenges.getRelatedSolutions(this.challengeData.id);
			this.relatedSolutionsLoading = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	generateFake(count: number): Array<number> {
		const indexes = [];
		for (let i = 0; i < count; i++) {
			indexes.push(i);
		}
		return indexes;
	}
}
